.home {
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  &__text-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;

    h1 {
      color: var(--yellow-theme-main-color);
      margin: 0px;
      font-weight: 400;
      font-size: 7rem;
      line-height: 10rem;
      position: relative;
      font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
      overflow: hidden;
      white-space: nowrap;
      animation: typingEffect 2.5s steps(30, end);
      
    }
    h2 {
      color: var(--yellow-theme-main-color);
      text-align: center;
      margin: 0px;
      font-weight: 400;
      font-size: 7rem;
      line-height: 10rem;
      position: relative;
      font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
      overflow: hidden;
      white-space: nowrap;
      animation: typingEffect 2.5s steps(30, end);
      
    }
  }
  .contact-me {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: cneter;
    margin-top: 10px;
    &__buttons-wrapper {
      display: flex;
      gap: 10px;
      
      button,
      a
       {
        cursor: pointer;
        text-decoration: none;
        padding: 10px 70px;
        font-size: 2rem;
        background: transparent;
        color: var(--yellow-theme-main-color);
        border: 1.5px solid var(--yellow-theme-main-color);
        border-radius: 4px;
        font-weight: 500;
        &:hover {
          color: black;
          background: var(--yellow-theme-main-color);
        }
      }
      
    }

    &__socials-wrapper {
      display: flex;
      gap: 10px;
      justify-content: center;
      

      svg {
        color: var(--yellow-theme-main-color);
        cursor: pointer;
      }
    }
  }
}

@media only screen and (max-width: 549px) {
  .home__text-wrapper h1 {
    font-size: 3rem;
    line-height: 4rem;
  }
}
@media only screen and (min-width: 550px) and (max-width: 780px) {
  .home__text-wrapper h1 {
    font-size: 4rem;
    line-height: 6rem;
  }
}

@media only screen and (min-width: 550px) and (max-width: 780px) {
  .home__text-wrapper h1 {
    font-size: 4rem;
    line-height: 6rem;
  }
}

@media only screen and (min-width: 781px) and (max-width: 990px) {
  .home__text-wrapper h1 {
    font-size: 6rem;
    line-height: 8rem;
  }
}

@keyframes typingEffect {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
