.about {
  &__content {
    display: flex;
    gap: 20px;
    padding: 20px;
    width: 100%;

    &__personal-wrapper {
      flex: 1;
      h3 {
        margin-bottom: 10px;
        font-size: 2.5rem;
        color: var(--yellow-theme-main-color);
        font-weight: 700;
      }

      h3.personalContent {
        padding-top: 20px;
      }

      p {
        color: var(--selected-theme-sub-text-color);
        font-size: 1.8rem;
        line-height: 1.6;
        padding-bottom: 20px;
        border-bottom: 1px dashed var(--yellow-theme-main-color);
      }

      ul > li {
        line-height: 1rem;
        margin-bottom: 15px;

        span.title {
          font-size: 18px;
          position: relative;
          display: inline-block;
          margin-right: 15px;
          padding-bottom: 1px;
          color: var(--selected-theme-sub-text-color);
        }

        span.value {
          border: none;
          font-size: 18px;
          position: relative;
          display: inline-block;
          margin-right: 15px;
          padding-bottom: 1px;
          color: var(--selected-theme-sub-text-color);
        }
      }
    }

    &__services-wrapper {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: relative;

      &__inner-content {
        width: 350px;
        height: 350px;
        position: relative;
        border-radius: 50%;
        border: 1.5px solid var(--yellow-theme-main-color);
        animation: rotation 10s linear 0.1s infinite;
        background: radial-gradient(
          160px,
          var(--yellow-theme-main-color),
          transparent 110%
        );
        

        div:nth-child(1) {
          position: absolute;
          top: -8%;
          left: 41%;
        }
        div:nth-child(2) {
          position: absolute;
          right: -10%;
          top: 41%;
        }
        div:nth-child(3) {
          position: absolute;
          bottom: -8%;
          left: 41%;
        }
        div:nth-child(4) {
          position: absolute;
          top: 41%;
          left: -8%;
        }
      }
    }
  }
}

@keyframes rotation {
  100% {
    transform: rotate(500deg);
  }
}


