@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&display=swap");

:root {
  --selected-theme-background-color: var(--yellow-theme-nav-background-color);
  --selected-theme-main-color: var(--yellow-theme-main-color);
  --selected-theme-sub-text-color: var(--selected-theme-sub-text-color);
  --selected-theme-nav-background-color: var( --yellow-theme-nav-background-color);

  --yellow-theme-background-color: #1d1d1d;
  --yellow-theme-main-color: #ffdd40;
  --selected-theme-sub-text-color: #ffffff;
  --yellow-theme-nav-background-color: #1d1d1d;

  --font-base:"Roboto Slab", serif;

}

*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

html{
  font-size: 62%;
  height: 100%;
}

body{
  background: var(--selected-theme-background-color);
  height: 100%;
}

#root{
  height: 100%;
}
