.contact {
  &__content {
    padding: 20px;
    &__header-text {
      display: inline-block;
      color: var(--selected-theme-sub-text-color);
      font-size: 22px;
      font-weight: 600;
      line-height: 1rem;
      letter-spacing: 0.5px;
      position: relative;
      text-transform: uppercase;
      padding: 5px 10px;
      border-bottom: 2px solid var(--yellow-theme-main-color);
    }
    &__form {
      margin-top: 30px;
      &__controlsWrapper {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 40px;
        margin-bottom: 30px;

        div {
          position: relative;
          .inputName,
          .inputEmail,
          .inputDescription {
            width: 100%;
            border: 2px solid var(--yellow-theme-main-color);
            padding: 15px 10px;
            transition: all 0.5s ease;
            position: relative;
            z-index: 1;
            background: transparent;
            color: var(--selected-theme-sub-text-color);
          }
          .inputName:focus,
          .inputDescription:focus,
          .inputEmail:focus {
            outline: none;
          }

          .inputName::placeholder,
          .inputEmail::placeholder,
          .inputDescription::placeholder {
            color: var(--yellow-theme-main-color);
          }
        }
      }

      button {
        cursor: pointer;
        padding: 10px 80px;
        font-size: 1.8rem;
        background: transparent;
        color: var(--yellow-theme-main-color);
        border: 1.5px solid var(--yellow-theme-main-color);
        border-radius: 4px;
        font-weight: 500;

        &:hover {
          color: black;
          background: var(--yellow-theme-main-color);
        }
      }
    }
  }
}
