.skills {
  &__content-wrapper {
    display: flex;
    padding: 20px;
    gap: 20px;

    &__inner-content {
      display: flex;
      flex: 1;
      flex-direction: column;
      align-items: center;

      &__category-text {
        display: inline-block;
        color: var(--selected-theme-sub-text-color);
        font-size: 20px;
        font-weight: 600;
        line-height: 1rem;
        letter-spacing: 0.5px;
        position: relative;
        text-transform: uppercase;
        padding: 5px 10px;
        border-bottom: 2px solid var(--yellow-theme-main-color);
      }

      div {
        width: 100%;
      }

      .progressbar-wrapper {
        padding-top: 50px;
        p {
          font-size: 1.6rem;
          font-weight: 500;
          margin: 0px 0px 0px 10px;
          text-transform: uppercase;
          color: var(--selected-theme-sub-text-color);
          margin-bottom: 10px;
        }
      }

     
    }
  }
}


@media only screen and (max-width: 760px){
    .skills{
        &__content-wrapper{
            flex-direction: column;
        }
    }
}